body,
html,
#root {
  height: 100%;
}

html {
  font-size: 18px;
}

h1{
  padding: .77rem 0;
  background-color: #010c3f;
  text-align: center;
  font-family: "Poppins" , sans-serif;
  font-size: 1.88rem;
  color: #fff;
  margin: 0px 0px;

}

h1 .highlight{
  color: #6c41ec;
}

h2 {
  font-family: "Poppins" , sans-serif;
  font-size: 1.55rem;
}

.App{
  height: 100%;
  padding: 0 17% 10% 17%;
  background-image: url("./1792303.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  font-family: "Work Sans",sans-serif;
  font-weight: 500;
  color: #fff;
}

.App-playlist{
  display:flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: 1020px){
  .App-playlist{
    align-items: center;
    flex-direction: column;
  }
}
